







import { Component, Vue } from 'vue-property-decorator';
import Success from './Success.vue';
import Failure from './Failure.vue';
import { getStorage, setPhone } from '../store';

@Component({
  name: 'Home',
  components: {
    Success,
    Failure,
  },
})
export default class Home extends Vue {
  data = null;

  isWin = 0;

  async mounted() {
    const storage = getStorage();
    if (!storage || !storage.phone) {
      this.$router.replace({ name: 'login' });
      return false;
    }
    const { phone } = storage;
    try {
      const user = await this.queryUser(phone) || {};
      if (!user.phone) {
        setPhone('');
        this.$router.replace({ name: 'login' });
        return;
      }
    } catch (error) {
      this.$router.replace({ name: 'login' });
      return;
    }
    try {
      const data = await this.$xhr.api.get('/users/lottery', {
        params: {
          phone,
        },
      });
      if (!data.switch) {
        this.$router.push({ name: 'result', query: { phone } });
        return;
      }
      if (data.isWin) {
        this.data = data;
        this.isWin = 1;
      } else {
        this.isWin = 2;
      }
    } catch (error) {
      this.$toast(error && error.msg);
    }
  }

  async queryUser(phone: string) {
    return this.$xhr.api.get('/users/getUser', {
      params: {
        phone,
      },
    });
  }
}
